.navbar {
  background: #fff !important;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  height: 65px;
}

.nav-container {
  display: flex;
  width: 100%;
  margin: auto;
}

/* .navbar-light .navbar-nav .nav-link:focus {
  color: #ca2128 !important;
} */
/* .navbar-light .navbar-nav .nav-link:not(:focus){
  color: black !important;
} */

.navbar-brand {
  padding-bottom: 0;
  margin-left: 7%;
  margin-right: 1.5vw;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: start;
  margin-bottom: 0;
  /* margin-right: 2rem; */
}

.nav-item {
  /* height: 80px; */
}

.nav-links {
  color: #777777;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 1024px) {
  .navbar-brand {
    margin-left: 5%;
  }
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: center;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  .navbar-brand {
    margin-left: unset !important;
  }
}

@media (min-width: 1367px) {
  .navbar-brand>SVG{
    width:9.5vw;
    height:2.2vw;
  }
  .navbar{
    height: 4.7vw;
  }
}
